import React, { Component } from "react";
import { extent } from "d3-array";
import BarChart from '../BarChart/BarChart';

import competitionsPlayers from '../playersInLeagues.json';
import teamsInfo from '../teams.json';

import "./teams.css";

const FIELDS = ['max','avg'];
const SORTBY = 'avg';
class TeamsComparison extends Component {
  constructor(props) {
    super(props);

    this.competitionsList = props.competitionsList.filter(d => {
      return d.leagueID.search(/(2|3)/gi) === -1;
    }).map(d => {
      const players = competitionsPlayers.find(l => l.league === d.league);
      if(players) {
        d.players = players.players;
      }
      return d;
    })
    // console.log(this.competitionsList)
    this.state = {}
  }
  async componentDidMount() {
    const res = await fetch(
      `assets/transfers/data/yearsSameTeamByTeam.json`
    );

    const teamsStats = await res.json();

    // console.log(teamsStats)
    this.setState({
      teamsStats: teamsStats.map(d => {
        const league = this.props.competitionsList.find(l => l.league === d.league);
        if(league) {
          d.country = league.country;
          d.leagueName = league.leagueName;
          d.players = league.players;
        }
        const team = teamsInfo.find(t => t.id === d.team);
        if(team) {
          d.teamName = team.name;
        }
        return d;
      }).filter(d => d.leagueName)
      .filter(d => d.players > this.props.playersThreshold).slice(0,20).sort((a,b) => b[SORTBY] - a[SORTBY]),
    })
  }

  render() {
    const {teamsStats} = this.state;


    if(!teamsStats) {
      return null;
    }

    const extents = {};
    FIELDS.forEach(field => {
      extents[field] = extent(teamsStats, d => d[field]);
    })

    return <div className="teams">
      <BarChart data={teamsStats.map(d => {
        d.key = `${d.team}-${d.country}`;
        d.title = <span>{d.teamName}<br/>{d.country}</span>;
        return d;
      })} fields={FIELDS} extents={extents} label="anni"/>
    </div>;

  }
}

export default TeamsComparison;



