module.exports = [{"chapter":{"index":0,"en":{"title":"The legend of King Kazu","text":"<a href=\"#\" data=\"Kazuyoshi Miura-Feb 26, 1967\" data-index=\"0\" data-chapter=\"preface\">Kazuyoshi Miura</a> (), known as <i>King Kazu</i>, he is a Japanese living legend footballer, holding the records for being the oldest footballer and goalscorer in worldwide professional leagues. He surpassed <a href=\"#\" data=\"Sir Stanley Matthews-Feb 1, 1915\" data-index=\"0\" data-chapter=\"preface\">Sir Stanley Matthews</a>, when he played at 50 years and 7 days during Yokohama vs V-Varen Nagasaki.","shortText":"<a href=\"#\" data=\"Kazuyoshi Miura-Feb 26, 1967\" data-index=\"0\" data-chapter=\"preface\">Kazuyoshi Miura</a>, Japanese superstar footballer, he holds the records for being the oldest footballer and goalscorer in worldwide professional leagues. He surpassed <a href=\"#\" data=\"Sir Stanley Matthews-Feb 1, 1915\" data-index=\"0\" data-chapter=\"preface\">Sir Stanley Matthews</a>, when he played at 50 years and 7 days during Yokohama vs V-Varen Nagasaki."},"it":{"title":"La leggenda di King Kazu","text":"<a href=\"#\" data=\"Kazuyoshi Miura-Feb 26, 1967\" data-index=\"0\" data-chapter=\"preface\">Kazuyoshi Miura</a> (), detto <i>King Kazu</i>, leggenda vivente del calcio giapponese e mondiale, detiene il record di giocatore professionista e marcatore pi&ugrave; anziano in attivit&agrave; al mondo. Ha battuto il record detenuto da <a href=\"#\" data=\"Sir Stanley Matthews-Feb 1, 1915\"  data-index=\"0\"  data-chapter=\"preface\">Sir Stanley Matthews</a>, quando, il 5 Marzo 2017, gioc&ograve; a 50 anni e sette giorni di et&agrave;, la partita tra la sua squadra, i Yokohama FC, e il V-Varen Nagasaki.","shortText":"<a href=\"#\" data=\"Kazuyoshi Miura-Feb 26, 1967\" data-index=\"0\" data-chapter=\"preface\">Kazuyoshi Miura</a>, eroe del calcio giapponese, con il record di giocatore e marcatore pi&ugrave; anziano in attivit&agrave; al mondo, il 5 Marzo 2017 ha battuto il primato di <a href=\"#\" data=\"Sir Stanley Matthews-Feb 1, 1915\" data-index=\"0\" data-chapter=\"preface\">Sir Stanley Matthews</a>, giocando a 50 anni e sette giorni in Yokohama - V-Varen Nagasaki."}},"id":"Kazuyoshi Miura-Feb 26, 1967","height":840},{"chapter":{"index":1,"en":{"title":"The One Team Men","text":"La carriera di un giocatore di calcio dura in media <b>otto</b> anni. Le <i>bandiere</i> come <a href=\"#\" data=\"Francesco Totti-Sep 27, 1976\" data-index=\"1\">Francesco Totti</a>, <a href=\"#\" data=\"Paolo Maldini-Jun 26, 1968\" data-index=\"1\">Paolo Maldini</a> e <a href=\"#\" data=\"Ryan Giggs-Nov 29, 1973\" data-index=\"1\">Ryan Giggs</a> sono entrati per sempre nell'olimpo del calcio come <i>one-team-man</i> per aver giocato tutta la loro carriera professionistica in una sola squadra."},"it":{"title":"I giocatori bandiera","text":"La carriera di un giocatore di calcio dura in media <b>otto</b> anni. Le <i>bandiere</i> come <a href=\"#\" data=\"Francesco Totti-Sep 27, 1976\" data-index=\"1\">Francesco Totti</a>, <a href=\"#\" data=\"Paolo Maldini-Jun 26, 1968\" data-index=\"1\">Paolo Maldini</a> e <a href=\"#\" data=\"Ryan Giggs-Nov 29, 1973\" data-index=\"1\">Ryan Giggs</a> sono entrati per sempre nell'olimpo del calcio come <i>one-team-man</i> per aver giocato tutta la loro carriera professionistica in una sola squadra."}},"id":"Francesco Totti-Sep 27, 1976","substories":[{"id":"Francesco Totti-Sep 27, 1976","name":"Francesco Totti","type":"short","descr":"<i>Er Pupone</i>, uno dei giocatori pi forti di sempre, nel corso della sua carriera professionistica ha sempre militato nella Roma, squadra di cui  stato capitano dal 1998 al 2017, per un totale record di 19 stagioni.","shortDescr":"Nel corso della sua carriera professionistica ha sempre militato nella Roma, squadra di cui  stato capitano dal 1998 al 2017, per un totale record di 19 stagioni, in cui ha giocato 619 partite e segnato 250 goal."},{"id":"Paolo Maldini-Jun 26, 1968","name":"Paolo Maldini","type":"short","descr":"Difensore di statura mondiale, annoverato fra i migliori nella storia del calcio, nel corso della sua carriera, durata 25 anni, ha vestito solo la maglia del Milan, con cui ha vinto 26 trofei: 7 scudetti, 1 Coppa Italia, 5 Supercoppe italiane, 5 Champions League, 5 Supercoppe europee, 2 Coppe Intercontinentali e 1 Coppa del mondo per club FIFA.","shortDescr":"Difensore di statura mondiale, annoverato fra i migliori nella storia del calcio, nel corso della sua carriera, durata 25 anni, ha vestito solo la maglia del Milan, con cui ha vinto 26 trofei."},{"id":"Ryan Giggs-Nov 29, 1973","name":"Ryan Giggs","type":"short","descr":"Dal 1990 al 2014 ha militato nel Manchester United, club di cui  primatista assoluto di presenze (963, di cui 159 nelle competizioni UEFA per club).<br/>Con 37 trofei conquistati,  il giocatore pi vincente nella storia del calcio inglese."}]},{"chapter":{"index":2,"en":{"title":"The Journeymen","text":"Sono quei giocatori che cambiano casacca numerose volte durante la loro carriera. In inglese vengono chiamati <i>journeyman</i>. Il record assoluto di trasferimenti &egrave; detenuto da <a href=\"#\" data=\"Sebastin Abreu-Oct 17, 1976\" data-index=\"2\">Sebastian Abreu</a>. Tra <i>giramondo</i> meno famosi, come <a href=\"#\" data=\"John Burridge-Dec 3, 1951\" data-index=\"2\">John Burridge</a> e <a href=\"#\" data=\"Marco Borriello-Jun 18, 1982\" data-index=\"2\">Marco Borriello</a>, troviamo uno dei pi&ugrave; grandi talenti di sempre, <a href=\"#\" data=\"Zlatan Ibrahimovic-Oct 3, 1981\" data-index=\"2\">Zlatan Ibrahimovic</a>.","shortText":"In inglese vengono chiamati <i>journeyman</i>. Il record assoluto di trasferimenti &egrave; detenuto da <a href=\"#\" data=\"Sebastin Abreu-Oct 17, 1976\" data-index=\"2\">Sebastian Abreu</a>. Tra <i>giramondo</i> meno famosi, come <a href=\"#\" data=\"John Burridge-Dec 3, 1951\" data-index=\"2\">John Burridge</a> e <a href=\"#\" data=\"Marco Borriello-Jun 18, 1982\" data-index=\"2\">Marco Borriello</a>, troviamo uno dei pi&ugrave; grandi talenti di sempre, <a href=\"#\" data=\"Zlatan Ibrahimovic-Oct 3, 1981\" data-index=\"2\">Zlatan Ibrahimovic</a>."},"it":{"title":"I giramondo del calcio","text":"Sono quei giocatori che cambiano casacca numerose volte durante la loro carriera. In inglese vengono chiamati <i>journeyman</i>. Il record assoluto di trasferimenti &egrave; detenuto da <a href=\"#\" data=\"Sebastin Abreu-Oct 17, 1976\" data-index=\"2\">Sebastian Abreu</a>. Tra <i>giramondo</i> meno famosi, come <a href=\"#\" data=\"John Burridge-Dec 3, 1951\" data-index=\"2\">John Burridge</a> e <a href=\"#\" data=\"Marco Borriello-Jun 18, 1982\" data-index=\"2\">Marco Borriello</a>, troviamo uno dei pi&ugrave; grandi talenti di sempre, <a href=\"#\" data=\"Zlatan Ibrahimovic-Oct 3, 1981\" data-index=\"2\">Zlatan Ibrahimovic</a>.","shortText":"In inglese vengono chiamati <i>journeyman</i>. Il record assoluto di trasferimenti &egrave; detenuto da <a href=\"#\" data=\"Sebastin Abreu-Oct 17, 1976\" data-index=\"2\">Sebastian Abreu</a>. Tra <i>giramondo</i> meno famosi, come <a href=\"#\" data=\"John Burridge-Dec 3, 1951\" data-index=\"2\">John Burridge</a> e <a href=\"#\" data=\"Marco Borriello-Jun 18, 1982\" data-index=\"2\">Marco Borriello</a>, troviamo uno dei pi&ugrave; grandi talenti di sempre, <a href=\"#\" data=\"Zlatan Ibrahimovic-Oct 3, 1981\" data-index=\"2\">Zlatan Ibrahimovic</a>."}},"id":"Zlatan Ibrahimovic-Oct 3, 1981","substories":[{"id":"Zlatan Ibrahimovic-Oct 3, 1981","name":"Zlatan Ibrahimovic","type":"short","descr":"Zlatan ha militato in sette campionati diversi. Considerato uno dei calciatori pi forti e completi al mondo, occupa il 9 posto della classifica dei marcatori della UEFA Champions League.<br/>Nella sua carriera ha cambiato nove maglie.","shortDescr":"Ha militato in sette campionati diversi. Considerato uno dei calciatori pi forti e completi al mondo, occupa il 9 posto della classifica dei marcatori della UEFA Champions League.<br/>Nella sua carriera ha cambiato nove maglie."},{"id":"Sebastin Abreu-Oct 17, 1976","name":"Sebastin \"Loco\" Abreu","type":"short","descr":"Il cileno <i>Loco</i> Abreu &egrave; &egrave; stato iscritto nel Guinnes dei Primati come il giocatore che ha cambiato pi&ugrave; squadre nella storia del calcio. Ha cambiato casacca ben <b>26</b> volte, visitando 11 nazioni, Uruguay, Argentina, Spagna, Brasile, Messico, Israele, Grecia, Ecuador, Paraguay, El Salvador e Chile."}]},{"chapter":{"index":3,"en":{"title":"The legends","text":"La storia del calcio &egrave; permeata di leggenda. Chi &egrave; stato il pi&ugrave; grande di sempre? <i>O rei</i> <a href=\"#\" data=\"Pel-Oct 23, 1940\" data-index=\"3\">Pel</a>? <i>El pibe de oro</i> <a href=\"#\" data=\"Diego Maradona-Oct 30, 1960\" data-index=\"3\">Maradona</a>? Il <i>Divin codino</i> <a href=\"#\" data=\"Roberto Baggio-Feb 18, 1967\" data-index=\"3\">Roberto Baggio</a>?, O forse <i>la pulga atomica</i> <a href=\"#\" data=\"Lionel Messi-Jun 24, 1987\" data-index=\"3\">Leo Messi</a>?"},"it":{"title":"Le leggende","text":"La storia del calcio &egrave; permeata di leggenda. Chi &egrave; stato il pi&ugrave; grande di sempre? <i>O rei</i> <a href=\"#\" data=\"Pel-Oct 23, 1940\" data-index=\"3\">Pel</a>? <i>El pibe de oro</i> <a href=\"#\" data=\"Diego Maradona-Oct 30, 1960\" data-index=\"3\">Maradona</a>? Il <i>Divin codino</i> <a href=\"#\" data=\"Roberto Baggio-Feb 18, 1967\" data-index=\"3\">Roberto Baggio</a>?, O forse <i>la pulga atomica</i> <a href=\"#\" data=\"Lionel Messi-Jun 24, 1987\" data-index=\"3\">Leo Messi</a>?"}},"id":"Pel-Oct 23, 1940","substories":[{"id":"Pel-Oct 23, 1940","name":"O Rei Pel","type":"short","descr":"<b>Pel</b>, il Calciatore del Secolo per la FIFA, l'unico calciatore al mondo ad aver vinto tre edizioni del campionato mondiale di calcio. A passato 17 anni al Santos, dove ha segnato 571 reti in 586 partite. La FIFA gli riconosce il record di reti realizzate in carriera, 1281 in 1363 partite."},{"id":"Diego Maradona-Oct 30, 1960","name":"Diego Armando Maradona","type":"short","descr":"<p><i>D10S, El pibe de oro, la mano de Dios, Dieguito, Pelusa</i>... forse il pi&ugrave; grande di ogni tempo. Nella sua carriera ha fatto sognare generazioni di tifosi. Ha militato in sei squadre diverse.</p><p><i>Tifosi di tutti i quartieri tradivano le loro squadre del cuore per vedere quel genio per il quale un fazzoletto di terreno era pi di un latifondo. Alcuni lo confusero con Dio, e quando sei poco pi che un bambino non hai motivo di mettere in dubbio lopinione dei grandi.</i><br/>(Jorge Valdano)</p>","shortDescr":"<p><i>D10S, El pibe de oro, la mano de Dios, Dieguito, Pelusa</i>... forse il pi&ugrave; grande di ogni tempo. Nella sua carriera ha fatto sognare generazioni di tifosi. Nella sua carriera ha militato in sei squadre diverse.</p>"}]}]

