import React, { Component } from "react";
import { extent, max } from "d3-array";
import { nest } from "d3-collection";
import BarChart from '../BarChart/BarChart';

import competitionsPlayers from '../playersInLeagues.json';
import teamsInfo from '../teams.json';

import "./players.css";

const FIELDS = ['max','avg'];
const SORTBY = 'avg';
class PlayersComparison extends Component {
  constructor(props) {
    super(props);

    this.competitionsList = props.competitionsList.filter(d => {
      return d.leagueID.search(/(2|3)/gi) === -1;
    }).map(d => {
      const players = competitionsPlayers.find(l => l.league === d.league);
      if(players) {
        d.players = players.players;
      }
      return d;
    }).filter(d => d.players > this.props.playersThreshold)
    // console.log(this.competitionsList)

    this.state = {}
  }
  async componentDidMount() {
    const res = await fetch(
      `assets/transfers/data/interestingPlayers.json`
    );

    const playersStats = await res.json();
    // playersStats = playersStats.filter(d => d._id.name === 'Marco Borriello')
    // console.log(playersStats)

    const stats = playersStats.map((d,i) => {
      d.teamsStats = {}
      let teamsIndex = {};
      let prevTeam = null;
      d.teams = d.teams.sort((a,b) => {
        if(a.year === b.year) {
          return a.joined - b.joined;
        }
        return a.year - b.year;
      });
      d.teams.forEach(team => {

        if(!teamsIndex[team.team]) {
          teamsIndex[team.team] = 0;
          //console.log('create index for',team.team,teamsIndex[team.team])
        }


          if(!prevTeam || (prevTeam !== team.team)) {
            if(!team.joined || (team.year !== team.joined)) { // this should prevent to count double a return from loan and change in the same year
              teamsIndex[team.team] += 1;
            }
          }

          const currentIndex = `${team.team}_${teamsIndex[team.team]}`;
          if(!d.teamsStats[currentIndex]) {
            d.teamsStats[currentIndex] = {
              team: team.team,
              years: 0,
              index: teamsIndex[team.team],
            }
          }
          d.teamsStats[currentIndex].years += 1;
          prevTeam = team.team;



      })
      return d;
    })
    // console.log('STATS',stats)
    this.setState({
      stats: stats.map(d => {
        d.longest = max(Object.values(d.teamsStats), d => d.years);
        d.longestTeam = Object.values(d.teamsStats).find(t => t.years === d.longest);
        d.longestTeam.teamName = d.longestTeam.team;
        const team = teamsInfo.find(t => t.id === d.longestTeam.team);
        if(team) {
          d.longestTeam.teamName = team.name;
        }
        d.numberOfTeams = Object.values(d.teamsStats).length;
        return d;
      })
    })
  }

  render() {
    const {stats} = this.state;


    if(!stats) {
      return null;
    }

    return <div className="players">
      <section>
        <h2>I giocatori bandiera</h2>
        <p>Anni nella stessa squadra</p>
        <BarChart data={stats.sort((a,b)=> {
          if(a.longest === b.longest) {
            return b.maxYear - a.maxYear;
          }
          return b.longest - a.longest
        }).slice(0,20).map(d => {
          d.key = d['_id'].key;
          d.title = <span>{d['_id'].name}<br/>{d.longestTeam.teamName}</span>;
          return d;
        })} fields={['longest']} extents={{longest:[0,25]}} label="anni"/>
      </section>
      <section>
        <h2>I giocatori che cambiano piu' squadre</h2>
        <p>Squadre cambiate</p>
        <BarChart data={stats.sort((a,b)=> {
          if(b.numberOfTeams === a.numberOfTeams) {
            return b.maxYear - a.maxYear;
          }
          return b.numberOfTeams - a.numberOfTeams;
        }).slice(0,20).map(d => {
          d.key = d['_id'].key;
          d.title = <span className="single">{d['_id'].name}</span>;
          return d;
        })} fields={['numberOfTeams']} extents={{numberOfTeams:[0,25]}} label="cambi"/>
      </section>
    </div>;

    // const values = { x: [], y: []};
    //
    // Object.values(distributions).forEach(distribution => {
    //   console.log(distribution)
    //   values.x=[...values.x, ...distribution.map(d => d.x0), ...distribution.map(d => d.x1)];
    //   values.y=[...values.y, ...distribution.map(d => d.n)];
    // })
    //
    // const extents = {
    //   x: extent(values.x),
    //   y: extent(values.y),
    // };
    //
    // return <div className="distributions">
    //   {Object.entries(distributions).filter(d => groupMap[d[0]]).map(d => {
    //
    //     const info = groupMap[d[0]];
    //     return <div key={d[0]} className="distribution">
    //       <h5>{info.title}</h5>
    //       <h6>{info.subtitle}</h6>
    //       <Histogram key={d[0]} data={d[1]} extents={extents}/>
    //     </div>;
    //
    //   })}
    // </div>
  }
}

export default PlayersComparison;



